import React, { useState, useEffect } from 'react'
import { Donut } from 'react-dial-knob';
import './controls.global.css';

import {ButtonGroup, Button} from 'react-bootstrap';
import '../menu/menu.global.css';

export interface KnobsProps {
  controls: number[];
  onChange: (
    control: number,
    val: number
  ) => void;
}

interface ControlInterface {
  currentVal: number,
  onChange: (
    val: number
  ) => void;
}

function Control (props: ControlInterface) {

  let {currentVal, onChange} = props;

  const [value, setValue] = React.useState(currentVal);

  useEffect( () =>{
    onChange(value);
  }, [value]);

  return <Donut
      diameter={50}
      min={0}
      max={100}
      step={1}
      value={value}
      theme={{
        donutColor: 'var(--highlightCol)',
        bgrColor: 'var(--bgGrey)',
        centerColor:'var(--bgGrey)',
        centerFocusedColor: 'var(--bgGrey)',
        donutThickness: 1,
      }}
      style={{
        display: 'inline-block',
      }}
      onValueChange={(val: number) => {setValue(val);}}
      ariaLabelledBy="my-label"
      spaceMaxFromZero={false} 
      >
    </Donut>
}

export function Knobs(props: KnobsProps) {

  const  {controls, onChange} = props;
  let [page, setPage] = useState(0);

  return (
    <div className="controlPane pane">
      <div className='knob' style={{marginLeft:'30px'}}>{page*4}
      <Control currentVal={controls[page*4]} onChange={(val)=>{onChange(page*4, val);}}/></div>
      <div className='knob'>{(page*4)+1}
      <Control currentVal={controls[(page*4)+1]} onChange={(val)=>{onChange((page*4)+1, val);}}/></div>
      <div className='knob'>{(page*4)+2}
      <Control currentVal={controls[(page*4)+2]} onChange={(val)=>{onChange((page*4)+2, val);}}/></div>
      <div className='knob'>{(page*4)+3}
      <Control currentVal={controls[(page*4)+3]} onChange={(val)=>{onChange((page*4)+3, val);}}/></div>
      <ButtonGroup style={{}}>
      <Button className="pageTabs" onClick={() => {setPage(0);}} variant="secondary" active={page==0?true:false}>0</Button>
      <Button className="pageTabs" onClick={() => {setPage(1);}} variant="secondary" active={page==1?true:false}>1</Button>
      <Button className="pageTabs" onClick={() => {setPage(2);}} variant="secondary" active={page==2?true:false}>2</Button>
      <Button className="pageTabs" onClick={() => {setPage(3);}} variant="secondary" active={page==3?true:false}>3</Button>
      <Button className="pageTabs" onClick={() => {setPage(4);}} variant="secondary" active={page==4?true:false}>4</Button>
      <Button className="pageTabs" onClick={() => {setPage(5);}} variant="secondary" active={page==5?true:false}>5</Button>
      <Button className="pageTabs" onClick={() => {setPage(6);}} variant="secondary" active={page==6?true:false}>6</Button>
      <Button className="pageTabs" onClick={() => {setPage(7);}} variant="secondary" active={page==7?true:false}>7</Button>
    </ButtonGroup>

    </div>
  );
}
