import {Buffer} from 'buffer';

export interface PatchContent{
    fwData: number[];
    userText: string;
    appData: string;
}

const PATCH_HEADER_POS = 0;
const PATCH_VERSION_MAJ_POS = 4;
const PATCH_VERSION_MIN_POS = 5;
const PATCH_SEC0_SIZE_POS = 6;
const PATCH_SEC1_SIZE_POS = 10;
const PATCH_SEC2_SIZE_POS = 14;
const PATCH_SEC0_DATA_POS = 18;

export function createPatchFile(patch: PatchContent)
{
    const headBuf = Buffer.from("HWPF", 'utf8');
    const vMajBuf = Buffer.allocUnsafe(1);
    const vMinBuf = Buffer.allocUnsafe(1)
    const s0s = Buffer.allocUnsafe(4)
    const s1s = Buffer.allocUnsafe(4)
    const s2s = Buffer.allocUnsafe(4)
    const s3s = Buffer.allocUnsafe(4)
    vMajBuf.writeUInt8(0 & 0xFF, 0);
    vMinBuf.writeUInt8(1 & 0xFF, 0);
    const fwdBuf = Buffer.from(patch.fwData);
    const utBuf  = Buffer.from(patch.userText, 'utf8');
    const adBuf  = Buffer.from(patch.appData, 'utf8');
    
    s0s.writeUInt32LE(fwdBuf.byteLength, 0);
    s1s.writeUInt32LE(utBuf.byteLength, 0);
    s2s.writeUInt32LE(adBuf.byteLength, 0);
    s3s.writeUInt32LE(1, 0);
    
    return Buffer.concat([headBuf, vMajBuf, vMinBuf, s0s, s1s, s2s, fwdBuf, utBuf, adBuf]);
  }

  export function readPatchFile(patchData: Buffer)
  {
    const data = Buffer.from(patchData);
    const header = new TextDecoder().decode(data.slice(PATCH_HEADER_POS, PATCH_VERSION_MAJ_POS));
    const vMaj = data.readUInt8(PATCH_VERSION_MAJ_POS);
    const vMin = data.readUInt8(PATCH_VERSION_MIN_POS);
    const s0s = data.readInt32LE(PATCH_SEC0_SIZE_POS);
    const s1s = data.readInt32LE(PATCH_SEC1_SIZE_POS);
    const s2s = data.readInt32LE(PATCH_SEC2_SIZE_POS);
    const fwData:number[] = [...data.slice(PATCH_SEC0_DATA_POS, PATCH_SEC0_DATA_POS+s0s )];
    const us: string = new TextDecoder().decode(data.slice(PATCH_SEC0_DATA_POS+s0s, PATCH_SEC0_DATA_POS+s0s+s1s));
    const appData: string = new TextDecoder().decode(data.slice(PATCH_SEC0_DATA_POS+s0s+s1s, PATCH_SEC0_DATA_POS+s0s+s1s+s2s));

    const patch: PatchContent = {
        fwData: fwData,
        userText: us,
        appData: appData,
    }

    console.log(header, vMaj, vMin, s0s, s1s, s2s );
    console.log(patch.userText);
    return patch;
  }