import {useState, useEffect} from 'react';
import Button from 'react-bootstrap/Button';
import {Buffer} from 'buffer';

  export interface SaveLoadLayoutProps {
    slots: number[];
    currentSlot: number;
    connected: boolean;
    onSlotLoad: (slot: number) => void;
    onSlotSave: (slot: number) => void;
    onSlotDelete: (slot: number) => void;
    onLocalLoad: (patchSlot: number, patchBuffer: Buffer) => void;
    onLocalSave: () => void;
  }

  export default function SaveLoadLayout(props: SaveLoadLayoutProps) {
    const { slots, currentSlot, connected, onSlotLoad, onSlotSave, onSlotDelete, onLocalSave, onLocalLoad } = props;

    const [selSlot, selectSlot] = useState(0);

    const [inputFile, setInputFile] = useState<HTMLInputElement | null>(null);
    useEffect(() => {
      setInputFile(document.getElementById("input-file") as HTMLInputElement);
    }, []);

    const handleUpload = () => {
      inputFile?.click();
    };

    const fileopened = () => {
      let f: File | undefined = inputFile?.files![0];
      if(f)
      {
        console.log(f.arrayBuffer);
        let buff  = f.arrayBuffer().then( (b)=>{
          onLocalLoad(0, new Buffer(b));
        });
      }
      
    };
  
    return(
        <div style={{ display: 'grid', gridTemplateColumns: 'inherit'}}>
            <div style={{ gridColumn: '1 / 5' }} className="menuTitle">Local Patches</div>
            <div style={{ gridColumn: '1 / 5', paddingBottom: '20px'}} >
              <input id="input-file" className="d-none" type="file" onChange={fileopened}/>
              <Button className="menuButton" variant="outline-secondary" onClick={() => onLocalSave()}>Save</Button>
              <Button className="menuButton" variant="outline-secondary" onClick={handleUpload}>Load</Button>
            </div>
            <div style={{height:'20px'}}/>
            <div style={{ gridColumn: '1 / 5' }} className="menuTitle">Device Patches</div>
            <div style={{ gridColumn: '1 / 5' }} className="menuSubtitle">Selected: {selSlot}</div>
            <SlotGrid connected = {connected} slotsState={slots} currentSlot={currentSlot} selected={selSlot} onClick={(sel:number)=>{selectSlot(sel);}}/>
            <div style={{ gridColumn: '1 / 5', paddingBottom: '20px'}} >
              <Button className="menuButton" variant="outline-secondary" onClick={() => onSlotSave(selSlot)}>Save</Button>
              <Button className="menuButton" variant="outline-secondary" onClick={() => onSlotLoad(selSlot)}>Load</Button>
              <Button className="menuButton" variant="outline-secondary" onClick={() => onSlotDelete(selSlot)}>Delete</Button>
            </div>
      </div>
    )
  }

            //onClick={() => onLocalLoad()}

  interface SlotGridProps {
    connected: boolean;
    slotsState: number[];
    selected: number;
    currentSlot: number;
    onClick: (selected: number) => void;
  }

  export function SlotGrid(props: SlotGridProps)
  {
    const {slotsState, onClick, selected, currentSlot, connected} = props;

    const slots: JSX.Element[] = [];


    for (let i = 0; i < 64; i++) {
        const cont = i;
        let taken = false;
        let current = false;
        let sel = false;
        if(connected)
        {
          taken = ((slotsState[i>>5] >> i)&0x0000000000000001)>0;
          sel = selected == i;
          current = currentSlot==i
        }

        slots.push(<SlotButton key={cont} selected={sel} taken={taken} currentSlot={current} onClick={()=>{onClick(i)}} text={i.toString()} ></SlotButton>)
    }

    return (
        <div style={{
          border: '1px white solid',
          borderRadius: '5px',
          width: '378px',
          height: '296px',
          padding:'10px', 
          paddingLeft:'10px', 
          display: "grid ", 
          gridTemplateColumns: '45px 45px 45px 45px 45px 45px 45px 45px', 
          gridTemplateRows: '35px 35px 35px 35px 35px 35px 35px 35px',
          marginBottom: '10px',
          marginLeft:'5px'
          }}>
            {slots}
        </div>
    );
  }

  interface SlotButtonProps {
    text: string;
    selected: boolean;
    currentSlot: boolean;
    taken: boolean;
    onClick: () => void;
  }

  export function SlotButton(props: SlotButtonProps) {
    const { selected, currentSlot, taken, onClick, text } = props;

    let fontCol = 'var(--unHighlightCol)';
    if(taken) {fontCol = 'var(--highlightCol)';}
    if(currentSlot) {fontCol = 'var(--accentCol)';}

    let borderCol = fontCol;
    let border = '1px';
    if(selected){
      border = '2px';
      if(!currentSlot)
      {
        borderCol = 'var(--highlightCol)';
      }
    }

    return (
      <div
        role="button"
        tabIndex={0}
        style={{
          borderColor: borderCol,
          color: fontCol,
          borderWidth: border,
          margin: selected? '-1px' : '0px',
          height: '30px',
          fontSize: '20px',
          width: '40px',
          textAlign:'center',
          lineHeight:'25px',
        }}
        className="slotButton"
        onClick={() => onClick()}
      >
        {text}
      </div>
    );
  }