import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';

import React, { useState, useRef, useEffect, useCallback } from 'react';
import ReactFlow, { Node, Edge, addEdge, updateEdge, NodeChange, EdgeChange, EdgeRemoveChange, EdgeAddChange, applyNodeChanges, applyEdgeChanges, OnSelectionChangeParams, Connection, ReactFlowProvider, Background, Controls, isEdge } from 'react-flow-renderer';
import { EditText } from 'react-edit-text';
import { Menu } from './menu/Menu';
import { nodeTypes, lib, objectIDs } from './library/Library';
import { Knobs } from './controls/Controls';
import { ObjectData, ObjectParam } from './library/LibraryTypes';
import GenericNode from './library/nodes/GenericNode';
import AudioEdge from './customised/AudioEdge';
import { computePatch, processListToBuffer } from './patch/computePatch';
import { readPatchFile, PatchContent, createPatchFile } from './patch/patchFile';
import SerialConnecton from './serial';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faXmark, faBars} from '@fortawesome/free-solid-svg-icons';

import { Buffer } from 'buffer';

const edgeTypes = {
  audioEdge: AudioEdge,
};

const initialNodes: Node<ObjectData>[] = [
  {
    id: 'audioIn',
    type: 'audioInput',
    data: { dspID: objectIDs.AUDIO_IN, params: [], inputBuffs: [], outputBuffs: [-1], patchIndex: 999 },
    position: { x: 500, y: 100 },
  },
  {
    id: 'audioOut',
    type: 'audioOutput',
    data: { dspID: objectIDs.AUDIO_OUT, params: [], inputBuffs: [-1], outputBuffs: [], patchIndex: 999},
    position: { x: 500, y: 600 },
  },
];

const anyNull: any = null;
let selIndex = 0;
let hardwareMode = false;
const seperator = '~%$^';

let graphUpdated = false;


function HotwireApp() {

  const reactFlowWrapper: any = useRef(null);
  const [reactFlowInstance, setReactFlowInstance]: any = useState(null);
  const [nodes, setNodes] = useState<Node<ObjectData>[]>(initialNodes);
  const [edges, setEdges] = useState<Edge[]>([]);
  const [controls, setControls] = useState(new Array(64).fill(0));
  const [selObj, setSelectedObj] = useState(anyNull);
  const [menuShow, setMenuVisible] = useState(false);
  const [menuTab, setMenuTab] = useState(0);
  const [auth, setAuth] = useState(true);
  // const [deviceConnected, setDeviceConnected] = useState(false);
  // const [deviceCpu, setDeviceCpu] = useState(0);
  // const [deviceRam, setDeviceRam] = useState(0);
  // const [deviceName, setDeviceName] = useState('-');
  const [deviceSlots, setDeviceSlots] = useState([0, 0]);
  const [deviceCurrentSlot, setDeviceCurrentSlot] = useState(0xFF);

  const getNewNodeId = (type: any): string => {
    return `${type}_${Math.floor(Date.now() * Math.random())}`;
  }

  const onNodeClick = (event: React.MouseEvent, node: Node) => {
    console.log('click', node.id);
  };

  const onNodeDoubleClick = (event: React.MouseEvent, node: Node) => {
    setMenuVisible(true);
    setMenuTab(1);
  };

  const onPaneClick = (_event: any) => { };

  const onNodesChange = useCallback(
    (changes: NodeChange[]) => setNodes((nds: any) => applyNodeChanges(changes, nds)),
    [setNodes]
  );

  const onEdgesChange = useCallback(
    (changes: EdgeChange[]) => {
      changes.forEach((change) => {
        console.log(change.type);
        if (change.type === 'remove' || change.type === 'add') {
          graphUpdated = true;
        }
      });
      setEdges((eds) => applyEdgeChanges(changes, eds));

    }, []
  );

  const onConnect = (connection: Connection) => {
    let valid = true;
    edges.forEach((edge) => {
      if (edge.target === connection.target && edge.targetHandle === connection.targetHandle) {
        valid = false;
      }
    });
    if (valid) {
      setEdges((eds) => addEdge(connection, eds));
      graphUpdated = true;
    }
  };

  const onEdgesDelete = useCallback(
    (edges: Edge[]) => { graphUpdated = true; }, []
  );

  const onEdgeUpdate = useCallback(
    (oldEdge: Edge, newConnection: Connection) => {
      let valid = true;
      edges.forEach((edge) => {
        if (edge.target === newConnection.target && edge.targetHandle === newConnection.targetHandle) {
          valid = false;
        }
      });
      if (valid) {
        setEdges((els) => updateEdge(oldEdge, newConnection, els));
        graphUpdated = true;
      }
    }, []
  );

  const onLoad = (reactFlowInstance: any) => {
    setReactFlowInstance(reactFlowInstance);
  }

  const onSelectionChange = ({ nodes, edges }: OnSelectionChangeParams) => {
    if (nodes.length === 1) {
      nodes.forEach((n, i) => {
        if (n.id === nodes[0].id) {
          setSelectedObj(n);
          selIndex = i;
        }
      });
    } else {
      setSelectedObj(null);
    }
  };

  const onParamChange = (
    params: ObjectParam[],
    index: number,
    valueOnly: boolean
  ) => {
    const updatedNodes = [...nodes];
    //updatedEles[selIndex].data.params = params;
    setNodes(updatedNodes);
    if (valueOnly) {
      //sendParamValueToDevice(updatedEles[selIndex].data.objectIdx, index, params[index].value);
    } else {
      //sendParamToDevice(updatedEles[selIndex].data.objectIdx, index, params[index]);
    }
  };

  const onControlChange = (
    allCtls: Number[] | null,
    control: number | null,
    val: number | null,
    name: string | null
  ) => {
    if (allCtls != null) {
      setControls(allCtls);
      return;
    }

    /* Make shallow copy */
    const ctls:number[] = [...controls];

    if (control != null) {
      if (control < controls.length) {
        /* Update and pass new values to callback */
        if (val !== null) {
          ctls[control] = val;
          /* send ctl data */
          // const ctrl: CtrlTX = {
          //   idx: control,
          //   value: val,
          // };
          // //sendControlToDevice(ctrl);
        }
      }

      setControls(ctls);

      setNodes((nodes) =>
        nodes.map((node) => {
          if (node.data !== undefined) {
            node.data.params.forEach((p: any) => {
              if (p.fixed === false && p.ctlIdx === control) {
                switch (p.mapShape) {
                  case 0: // Linear
                    p.value = ((ctls[control] / 100) * (p.mapMax - p.mapMin) + p.mapMin).toFixed(2);
                    break;
                  case 1: // Exp
                    p.value = ((ctls[control] / 100) * (ctls[control] / 100) * (p.mapMax - p.mapMin) + p.mapMin).toFixed(2);
                    break;
                  case 2: // Parabola
                    {
                      const p1 = (ctls[control] - 50) / 5;
                      p.value = (((100 - p1 * p1) / 100) * (p.mapMax - p.mapMin) + p.mapMin).toFixed(2);
                    }
                    break;
                  default:
                    break;
                }
              }
            });
          }
          return node;
        })
      );
    }
  };

  const onDeviceControlChange = (control: number, value: number) => {
    const controlsCopy = [...controls];
    controlsCopy[control].value = value;
    setControls(controlsCopy);
  };

  const onDragOver = (event: any) => {
    event.preventDefault();
    event.dataTransfer.dropEffect = 'move';
  };

  const onDrop = (event: any) => {
    event.preventDefault();
    const reactFlowBounds = reactFlowWrapper.current.getBoundingClientRect();
    const dspID: number = parseInt(event.dataTransfer.getData('application/reactflow'));

    /* This prevents a crash when selecting multiple items.. */
    let objDesc = lib.objectDescriptors.get(dspID);

    if (!dspID || !objDesc) {
      return;
    }

    const position = reactFlowInstance.project({
      x: event.clientX - reactFlowBounds.left,
      y: event.clientY - reactFlowBounds.top,
    });

    /* Initialise new object data */
    const objectData: ObjectData = {
      dspID: dspID,
      params: [],
      inputBuffs: [],
      outputBuffs: [],
      patchIndex: 0xff,
    };

    /* Init buffer list as 'not connected' */
    objDesc.inPortList.forEach((_p, i) => {
      objectData.inputBuffs[i] = -1;
    });
    objDesc.outPortList.forEach((_p, i) => {
      objectData.outputBuffs[i] = -1;
    });

    /* Add default params */
    objDesc.paramList.forEach((p) => {
      objectData.params.push({
        fixed: true,
        value: p.default,
        ctlIdx: 0,
        mapMin: p.min,
        mapMax: p.max,
        mapShape: 0,
      });
    });

    /* Create and add new object */
    const newNode = {
      id: getNewNodeId(objDesc.name),
      type:'generic',
      position,
      data: objectData,
      selectable: true,
    };

    setNodes((es) => es.concat(newNode));
  };

  const savePatch = (toDevice: boolean, slot: number) => {
    const flow = reactFlowInstance.toObject();
    const appData: string = JSON.stringify(flow) + seperator + JSON.stringify(controls);
    const fwData = processListToBuffer(computePatch(nodes, edges, false));
    const us: string = "Test patch";
    const patchBuffer = createPatchFile({fwData: fwData, userText:us, appData:appData});

    const a = document.createElement('a');
    a.setAttribute('style', 'display: none');
    document.body.appendChild(a);
    //const view = new Uint8Array(Module.HEAPU8.buffer, dataPtr, size);
    const blob = new Blob([patchBuffer], {
      type: 'octet/stream'
      });
    const url = window.URL.createObjectURL(blob);
    a.href = url;
    const filename = "Patch.hwp"
    a.download = filename;
    if (window.confirm("Do you want to download this file?"))
    {
      a.click();
    }
    window.URL.revokeObjectURL(url);
    document.body.removeChild(a);

    // if(toDevice)
    // {
    //   //savePatchFileToDevice(slot, patchBuffer);
    // }
    // else
    // {
    //  // window.electron.ipcRenderer.send('saveFile', ["patch", patchBuffer]);
    // }

  }

  const loadPatch = (fromDevice: boolean, slot: number) => {
    if (fromDevice) {
      // loadPatchFileFromDevice(slot);
    }
    else {

    }
  };

  const deletePatch = (slot: number) => {
    //deletePatchFileFromDevice(slot);
  }

  const onPatchData = (patchSlot: number, patchBuffer: Buffer) => {
    const  pc: PatchContent  = readPatchFile(patchBuffer);
    const settings = pc.appData.split(seperator);

    const flow = JSON.parse(settings[0]);
    const ctrls = JSON.parse(settings[1]);

    if (flow) {
      setNodes(flow.nodes || []);
      setEdges(flow.edges || []);
      //const { x = 0, y = 0, zoom = 1 } = flow.viewport;
      //setViewport({ x, y, zoom });
      setControls(ctrls);
    }
  };

  const exportConfig = () => {
    if (hardwareMode) {
      //sendDSPListToDevice(computePatch(elements));
    } else {
      computePatch(nodes, edges, true);
    }

  };

  useEffect(() => {
    if (graphUpdated === true) {
      graphUpdated = false;
      setDeviceCurrentSlot(0xFF);
      exportConfig();
    }
  }, [graphUpdated === true]);

  return (
    <div style={{ height: '100vh' }}>
      <ReactFlowProvider>
        <div
          style={{ height: '100vh' }}
          className="reactflow-wrapper"
          ref={reactFlowWrapper}
        >
          <ReactFlow
            style={{ background: '#171620' }}
            nodes={nodes}
            edges={edges}
            onNodesChange={onNodesChange}
            onEdgesChange={onEdgesChange}
            onNodeClick={onNodeClick}
            onNodeDoubleClick={onNodeDoubleClick}
            onSelectionChange={onSelectionChange}

            onConnect={onConnect}
            onEdgeUpdate={onEdgeUpdate}
            onEdgesDelete={onEdgesDelete}

            onInit={onLoad}
            onDrop={onDrop}
            onDragOver={onDragOver}
            onPaneClick={onPaneClick}
            nodeTypes={nodeTypes}
          >
          <div className="titleBar">
            <div className="menuIcon titleBar-item" style={{height:'35px'}} onClick={() => setMenuVisible(!menuShow)}>
              {menuShow ? <FontAwesomeIcon icon={faXmark} /> : <FontAwesomeIcon icon={faBars} />}
            </div>
            <div className="titleBar-item hotwire-logo">  h o t w i r e </div>
            <SerialConnecton onPatchLoadCallback={onPatchData}/>
          </div>
            <Menu
              show={menuShow}
              activeTab={menuTab}
              controls={controls}
              selObj={selObj}
              slots={deviceSlots}
              currentSlot={deviceCurrentSlot}
              connected={true}
              onSetVisible={setMenuVisible}
              onSetActiveTab={setMenuTab}
              onParamChange={onParamChange}
              onDeviceSave={(slot: number) => savePatch(true, slot)}
              onDeviceLoad={(slot: number) => loadPatch(true, slot)}
              onDeviceDelete={(slot: number) => deletePatch(slot)}
              onLocalSave={() => savePatch(false, 0)}
              onLocalLoad={onPatchData}
            />
            <Background gap={12} size={1} color="#000000" />
            <Knobs controls={controls} onChange={(control, val)=>{onControlChange(null, control, val, null);}} />
            <Controls />
          </ReactFlow>
        </div>
      </ReactFlowProvider>
    </div>
  );
}

export default function App() {

  const notSupported = document.getElementById('notSupported');
  notSupported?.classList.toggle('hidden', 'serial' in navigator); 

  return (
    <HotwireApp />
  );
}
