import { Handle, Position, NodeProps } from 'react-flow-renderer';
import { ObjectDesc } from '../LibraryTypes';
import './node.global.css';

export const gainDesc: ObjectDesc = {
  dspID: 2,
  name: 'Gain',
  desc: 'Gain',
  category: 'Mix',
  //nodeType: 'gain',
  inPortList: [
    { name: 'Audio in', desc: 'Audio to be amplified or attenuated', type: 0 },
  ],
  outPortList: [{ name: 'Audio out', desc: 'Audio output', type: 0 }],
  paramList: [{ name: 'Gain', desc: 'Gain', min: 0, max: 1, default: 1 }],
};

export default function GainNode(props: NodeProps) {
  const { selected, data } = props;
  const selectedCss = selected ? 'selected' : '';
  const portStyle = { width: '10px', height: '10px', background: '#555' };

  return (
    <>
      {gainDesc.inPortList.map((_p, i) => {
        const ci = i.toString();
        return (
          <Handle
            id={ci}
            key={ci}
            type="target"
            position={Position.Top}
            style={portStyle}
            //  onConnect={(params) => console.log('handle onConnect', params)}
          />
        );
      })}
      <div className={`nodeStyle ${selectedCss}`}>
        <div className="ObjUiTitle" style={{ paddingBottom: '5px' }}>
          {data.name}
        </div>
        <hr className={`objSepStyle ${selectedCss}`} />

        {gainDesc.paramList.map((pl, i) => {
          return (
            <div key={pl.name}>
              <div
                className="ObjUiParamName"
                style={{ width: '70px', textAlign: 'right' }}
              >
                {pl.name}
              </div>
              <div
                className="ObjUiParamVal"
                style={{
                  paddingLeft: '10px',
                  width: '70px',
                  textAlign: 'left',
                }}
              >
                {data.params[i].value}
              </div>
            </div>
          );
        })}
      </div>
      {gainDesc.outPortList.map((p) => {
        return (
          <Handle
            key={p.name.toString()}
            type="source"
            position={Position.Bottom}
            style={portStyle}
            //  onConnect={(params) => console.log('handle onConnect', params)}
          />
        );
      })}
    </>
  );
}
