import { Handle, Position, NodeProps } from 'react-flow-renderer';
import { ObjectDesc } from '../LibraryTypes';
import './node.global.css';

export const shelfDesc: ObjectDesc = {
  dspID: 11,
  name: 'Shelving Shaper',
  desc: 'Shelving Shaper',
  category: 'Audio Effects',
  //nodeType: 'shelf',
  inPortList: [{ name: 'Audio in', desc: 'Audio to be filtered', type: 0 }],
  outPortList: [{ name: 'Audio out', desc: 'Filtered audio', type: 0 }],
  paramList: [
    {
      name: 'Lo Freq',
      desc: 'Lo Frequency',
      min: 100,
      max: 1000,
      default: 1000,
    },
    {
      name: 'Hi Freq',
      desc: 'Hi Frequency',
      min: 2000,
      max: 20000,
      default: 2000,
    },
    { name: 'LF Gain', desc: 'HF gain', min: -12, max: 12, default: 0 },
    { name: 'HF Gain', desc: 'LF gain', min: -12, max: 12, default: 0 },
    { name: 'LF/HF Mix', desc: 'Mix', min: 0, max: 1, default: 0 },
  ],
};

export default function ShelfNode(props: NodeProps) {
  const { selected, data } = props;
  const selectedCss = selected ? 'selected' : '';
  const portStyle = { width: '10px', height: '10px', background: '#555' };

  return (
    <>
      {shelfDesc.inPortList.map((_p, i) => {
        const ci = i.toString();
        return (
          <Handle
            id={ci}
            key={ci}
            type="target"
            position={Position.Top}
            style={portStyle}
            //  onConnect={(params) => console.log('handle onConnect', params)}
          />
        );
      })}
      <div className={`nodeStyle ${selectedCss}`}>
        <div className="ObjUiTitle" style={{ paddingBottom: '5px' }}>
          {data.name}
        </div>
        <hr className={`objSepStyle ${selectedCss}`} />

        {shelfDesc.paramList.map((pl, i) => {
          return (
            <div key={pl.name.toString()}>
              <div
                className="ObjUiParamName"
                style={{ width: '70px', textAlign: 'right' }}
              >
                {pl.name}
              </div>
              <div
                className="ObjUiParamVal"
                style={{
                  paddingLeft: '10px',
                  width: '70px',
                  textAlign: 'left',
                }}
              >
                {props.data.params[i].value}
              </div>
            </div>
          );
        })}
      </div>
      {shelfDesc.outPortList.map((p) => {
        return (
          <Handle
            key={p.name.toString()}
            type="source"
            position={Position.Bottom}
            style={portStyle}
            //  onConnect={(params) => console.log('handle onConnect', params)}
          />
        );
      })}
    </>
  );
}
