import { Handle, Position, NodeProps } from 'react-flow-renderer';
import { ObjectDesc } from '../LibraryTypes';
import './node.global.css';

export const matrix4Desc: ObjectDesc = {
  dspID: 7,
  name: 'Matrix4',
  desc: '4 Channel matrix for testing',
  category: 'Mix',
  //nodeType: 'matrix4',
  inPortList: [
    { name: 'Audio in 1', desc: 'Audio ch 1', type: 0 },
    { name: 'Audio in 2 input', desc: 'Audio ch 2 input', type: 0 },
    { name: 'Audio in 3 input', desc: 'Audio ch 3 input', type: 0 },
    { name: 'Audio in 4 input', desc: 'Audio ch 4 input', type: 0 },
  ],
  outPortList: [
    { name: 'Audio out 1', desc: 'Audio output', type: 0 },
    { name: 'Audio out 1', desc: 'Audio output', type: 0 },
    { name: 'Audio out 1', desc: 'Audio output', type: 0 },
    { name: 'Audio out 1', desc: 'Audio output', type: 0 },
  ],
  paramList: [],
};

export default function Matrix4Node(props: NodeProps) {
  const { selected, data } = props;
  const selectedCss = selected ? 'selected' : '';
  //  const portStyle = { width: '10px', height: '10px', background: '#555' };

  return (
    <>
      {matrix4Desc.inPortList.map((_p, i) => {
        const ci = i.toString();
        return (
          <Handle
            key={ci}
            id={ci}
            type="target"
            position={Position.Top}
            style={{
              width: '10px',
              height: '10px',
              background: '#555',
              left: i * 35 + 35,
            }}
            //  onConnect={(params) => console.log('handle onConnect', params)}
          />
        );
      })}
      <div className={`nodeStyle ${selectedCss}`}>
        <div className="ObjUiTitle" style={{ paddingBottom: '5px' }}>
          {data.name}
        </div>
        <hr className={`objSepStyle ${selectedCss}`} />

        {matrix4Desc.paramList.map((pl, i) => {
          return (
            <div key={pl.name.toString()}>
              <div
                className="ObjUiParamName"
                style={{ width: '70px', textAlign: 'right' }}
              >
                {pl.name}
              </div>
              <div
                className="ObjUiParamVal"
                style={{
                  paddingLeft: '10px',
                  width: '70px',
                  textAlign: 'left',
                }}
              >
                {props.data.params[i].value}
              </div>
            </div>
          );
        })}
      </div>
      {matrix4Desc.outPortList.map((p, i) => {
        return (
          <Handle
            key={p.name.toString()}
            id={p.name.toString()}
            type="source"
            position={Position.Bottom}
            style={{
              width: '10px',
              height: '10px',
              background: '#555',
              left: i * 35 + 35,
            }}
            //  onConnect={(params) => console.log('handle onConnect', params)}
          />
        );
      })}
    </>
  );
}
