import { Node, Edge, getConnectedEdges, getIncomers, getOutgoers } from 'react-flow-renderer';
import { ObjectParam, ObjectData } from '../library/LibraryTypes';
import { lib } from '../library/Library';

export interface Process {
  dspID: number;
  inputBuffs: number[];
  outputBuffs: number[];
  params: ObjectParam[];
}

  const hasInputEdge = (node: Node, edges: Edge[]): boolean => {
    const links = getConnectedEdges([node], edges);
    let hasEdge = false;
    links.forEach((lnk) => {
      if (lnk.target === node.id) {
        hasEdge = true;
      }
    });
    return hasEdge;
  };

  function getFloat32Byte(float: number, byte: number): number {
    if (byte > 3) {
      return 0;
    }
    const buf = new ArrayBuffer(4);
    const dv = new DataView(buf);
    dv.setFloat32(0, Math.fround(float), true);
    return dv.getUint8(byte);
  }

  export function processListToBuffer(processList: Process[]) {
    const dataBuff: number[] = [];
  
    processList.forEach((proc) => {
      // dsp ID
      dataBuff.push((proc.dspID >> 8) & 0xff);
      dataBuff.push(proc.dspID & 0xff);
      // Input buffers
      proc.inputBuffs.forEach((ib) => {
        dataBuff.push((ib >> 8) & 0xff);
        dataBuff.push(ib & 0xff);
      });
      // Output buffers
      proc.outputBuffs.forEach((ob) => {
        dataBuff.push((ob >> 8) & 0xff);
        dataBuff.push(ob & 0xff);
      });
      // Parameters
      proc.params.forEach((param) => {
        // eslint-disable-next-line @typescript-eslint/no-unused-expressions
        param.fixed ? dataBuff.push(0x01) : dataBuff.push(0x00);
        dataBuff.push(getFloat32Byte(param.value, 3));
        dataBuff.push(getFloat32Byte(param.value, 2));
        dataBuff.push(getFloat32Byte(param.value, 1));
        dataBuff.push(getFloat32Byte(param.value, 0));
  
        dataBuff.push((param.ctlIdx >> 8) & 0xff);
        dataBuff.push(param.ctlIdx & 0xff);
  
        dataBuff.push(getFloat32Byte(param.mapMin, 3));
        dataBuff.push(getFloat32Byte(param.mapMin, 2));
        dataBuff.push(getFloat32Byte(param.mapMin, 1));
        dataBuff.push(getFloat32Byte(param.mapMin, 0));
  
        dataBuff.push(getFloat32Byte(param.mapMax, 3));
        dataBuff.push(getFloat32Byte(param.mapMax, 2));
        dataBuff.push(getFloat32Byte(param.mapMax, 1));
        dataBuff.push(getFloat32Byte(param.mapMax, 0));
  
        dataBuff.push(param.mapShape & 0xff);
      });
      // Seperator
      dataBuff.push(0xff);
    });

    return dataBuff;
  }

export const computePatch = (nodesIn: Node[], edgesIn: Edge[], debug: Boolean) => {

    const processList: Process[] = [];    // ← The final process list to fill
    //const inputlessNodeList: Node[] = []; // ← Set of all nodes with no incoming edge (and at least one outgoing edge, although perhaps some nodes won't have outputs?)
    let nextFreeBuff = 0;                 // ← Contains the index of the next buffer when needed. Init to 1 because audio out has 0.
    const freeBuffs: number[] = [];       // ← Contains the index of any buffers that have become available

    // Stuff for loop detection and removal:  https://www.geeksforgeeks.org/detect-cycle-in-a-graph/
    const visited: boolean[] = [];        // ← Whether a node index has been visited
    const recStack: boolean[] = [];       // ← Whether a node index is in the recursion stack
    let loopFound = false;                // ← Whether a loop has been detected
    let loopEdgeSourceNodeIdx = 0;        // ← Detected loop problematic edge source node index
    let loopEdgeTargetNodeIdx = 0;        // ← Detected loop problematic edge target node index
    const loopbackBuffs: number[] = [];   // ← Audio buffers used for loops. 

    let SortedIdx = 0;
  
    // Shallow copy nodes and edges by reference so that we can remove items as we go
    let edges: Edge[] = [...edgesIn];
    let nodes: Node<ObjectData>[] = [...nodesIn]; 

    const getBuffer = ():number => {
      let buff = freeBuffs.shift();
      if(buff == undefined){
        buff = nextFreeBuff++;
      }
      return buff;
    }

    const fillProcessList = () => {
         
      /* Initialise inputless nodes list */
      let inputlessNodeList = nodes.filter(node => !getIncomers(node, nodes, edges).length);

      /* while inputlessNodeList is not empty */
      while (inputlessNodeList.length) {
        /* Remove a node n from inputlessNodeList and add it to process list */
        let currentNode: Node = inputlessNodeList.shift()!;
        /* Assign buffers to each connected output of the node */
        // eslint-disable-next-line @typescript-eslint/no-loop-func
        currentNode.data.outputBuffs.forEach((_buff: number, buffIndex: number) => {
          edges.forEach((edge: any) => {
            /* If relevant output buffer is not already assigned, assign buffer */
            if (edge.source === currentNode.id && +edge.sourceHandle === buffIndex && currentNode.data.outputBuffs[buffIndex] < 0) {
              currentNode.data.outputBuffs[buffIndex] = getBuffer();
            }
          });
        });

        /* Remove node from nodelist so we can keep track of what's left */
        let nodeIndex = nodes.length - 1;
        while (nodeIndex >= 0) {
          if (nodes[nodeIndex].id === currentNode.id) {
            nodes.splice(nodeIndex, 1);
          }
          // eslint-disable-next-line no-plusplus
          nodeIndex--;
        }

        /* Add this node to the process list */
        if (currentNode.type !== 'audioOutNode' || nodes.length === 0) {
          const proc: Process = {
            dspID: currentNode.data.dspID,
            inputBuffs: currentNode.data.inputBuffs,
            outputBuffs: currentNode.data.outputBuffs,
            params: currentNode.data.params,
          };
          processList.push(proc);
          // Assign object processing list index
          currentNode.data.objectIdx = SortedIdx;
          SortedIdx += 1;
        }

        /* Remove edge, assign buffers, and if the node has no otherinputs, insert into inputless list */
        /* (Working backwards so that we can remove edges from the edglist as we go) */
        let edgeIndex = edges.length - 1;
        while (edgeIndex >= 0) {
          /* For each outgoing edge of this inputless node */
          if (edges[edgeIndex].source === currentNode.id) {
            /* Get target and source node and port for the edge before we remove it from the list */
            const { target } = edges[edgeIndex];
            const targetInPort: number = +edges[edgeIndex].targetHandle!;
            const sourcePort: number = +edges[edgeIndex].sourceHandle!;

            /* Remove edge from edgeList */
            edges.splice(edgeIndex, 1);

            /* For each node with an edge currentNode to node */
            // eslint-disable-next-line @typescript-eslint/no-loop-func
            nodes.forEach((node, nodeIndex) => {
              if (node.id === target) {
                /* Assign input buffer to target port from source port */
                node.data.inputBuffs[targetInPort] = currentNode.data.outputBuffs[sourcePort];

                /* If node has no other edges, insert into inputlessNodeList */
                if (!hasInputEdge(node, edges)) {
                  inputlessNodeList.push(node);
                }
              }
            });
          }
          edgeIndex -= 1;
        }

        /* Free some buffers if possible */
        /* For each input buffer of node n, check if there are any other remaining nodes still using that buffer. If not, it can be freed */
        currentNode.data.inputBuffs.forEach((b: any) => {
          let buffStillNeeded = false;
          if (b !== -1) {
            nodes.forEach((node) => {
              node.data.inputBuffs.forEach((ib: number) => {
                if (ib === b) {
                  buffStillNeeded = true;
                }
              });
              // Check output buffs too, becasue there may be loop back assigned buffs
              node.data.outputBuffs.forEach((ob: number) => {
                if (ob === b) {
                  buffStillNeeded = true;
                }
              });
            });
            // If it's not still needed and isn't a feedback buffer, or already in the free buffs list, add it!
            if ( buffStillNeeded === false
               && loopbackBuffs.indexOf(b) === -1 
               && freeBuffs.indexOf(b) === -1
              ) {
              freeBuffs.push(b);
            }
          }
        });
      }
    };

    const isCyclicUtil = (parentNodeIdx: number): Boolean => {
      if (visited[parentNodeIdx] === false) {
        // Mark the current node as visited and part of recursion stack
        visited[parentNodeIdx] = true;
        recStack[parentNodeIdx] = true;
        
        // Populate array with child nodes indicies of this node
        let childNodeIndicies: number[] = [];
        const childNodes: Node[] = getOutgoers(nodes[parentNodeIdx], nodes, edges);
        childNodes.forEach((childNode) => {
          nodes.forEach((node, nodeIdx) => {
            if(node.id === childNode.id) {
              childNodeIndicies.push(nodeIdx);
            }
          });
        })

        // Recur for all the child nodes of this node
        childNodeIndicies.forEach((childNodeIdx) => {
          if (!visited[childNodeIdx] && isCyclicUtil(childNodeIdx)) {
            if (!loopFound) {
              loopEdgeSourceNodeIdx = parentNodeIdx;
              loopEdgeTargetNodeIdx = childNodeIdx;
            }
            loopFound = true;
          }
          if (recStack[childNodeIdx] === true) {
            if (!loopFound) {
              loopFound = true;
              loopEdgeSourceNodeIdx = parentNodeIdx;
              loopEdgeTargetNodeIdx = childNodeIdx;
            }
            loopFound = true;
          }
        });
        if(loopFound) {return true;}
      }
      recStack[parentNodeIdx] = false; // remove the node from recursion stack
      return false;
    };

    const isCyclic = (): boolean => {
      // Mark all the nodes as not visited and not part of recursion stack
      nodes.forEach((_node, nodeIndex) => {
        visited[nodeIndex] = false;
        recStack[nodeIndex] = false;
      });

      // Call the recursive helper function to detect cycle in different DFS trees
      loopFound = false;
      nodes.forEach((_node, nodeIndex) => {
        if (isCyclicUtil(nodeIndex) === true) {
          console.log('Loop!');
          loopFound = true;
        }
      });

      if(debug && !loopFound){console.log('No loops');}
      return loopFound;
    };

    const removeLoops = () => {
      // While there are loops, remove the offending edges
      while (isCyclic()) {
        let edgeN = edges.length - 1;
        while (edgeN >= 0) {
          if (
            edges[edgeN].target === nodes[loopEdgeTargetNodeIdx].id &&
            edges[edgeN].source === nodes[loopEdgeSourceNodeIdx].id
          ) {
            // Assign buffers to replace offending edge..
            nodes[loopEdgeSourceNodeIdx].data.outputBuffs[parseInt(edges[edgeN].sourceHandle!)] = nextFreeBuff;
            nodes[loopEdgeTargetNodeIdx].data.inputBuffs[parseInt(edges[edgeN].targetHandle!)] = nextFreeBuff;
            loopbackBuffs.push(nextFreeBuff);
            // eslint-disable-next-line no-plusplus
            nextFreeBuff++;

            if(debug){
              console.log(`Removing offending edges from ${edges[edgeN].source} to ${edges[edgeN].target}`);
            }
            edges.splice(edgeN, 1);
          }
          // eslint-disable-next-line no-plusplus
          edgeN--;
        }
      }
    };

    /* Reset assigned buffers */
    nodes.forEach((node) => {
        node.data.inputBuffs.forEach((_b: number, bI: number) => {
          node.data.inputBuffs[bI] = -1;
        });
        node.data.outputBuffs.forEach((_b: number, bI: number) => {
          node.data.outputBuffs[bI] = -1;
        });
    });

    /* Ensure no egde source/target handles are null */
    /* Annoyingly reactflow sets handles to null if there's only one. */ 
    edges.forEach((edge) => {
      if (edge.sourceHandle === null) {
        edge.sourceHandle = '0';
      }
      if (edge.targetHandle === null) {
        edge.targetHandle = '0';
      }
    })

    if(debug){console.log('\nLooking for loops..');}
    removeLoops();

    if(debug){console.log('Filling process list..');}
    fillProcessList();

    if(debug){
    console.log('finished.');

      interface printPatch {
        dspId: number;
        inBuffs: string;
        outBuffs: string;
        params: string;
      }

      const tableData: printPatch[] = [];
      console.log('Process List:');
      processList.forEach((p: Process, i) => {
        let paramsString = '';
        p.params.forEach((pm) => {
          paramsString += `${pm.value}, `;
        });

        tableData[i] = {
          dspId: p.dspID,
          inBuffs: p.inputBuffs.toString(),
          outBuffs: p.outputBuffs.toString(),
          params: paramsString,
        };
      });
      console.table(tableData);
    }

    return processList;
  };