import { Handle, Position, NodeProps } from 'react-flow-renderer';
import { ObjectDesc } from '../LibraryTypes';
import { lib } from '../Library';
import './node.global.css';

export const inputDesc: ObjectDesc = {
  dspID: 0,
  name: 'Input',
  desc: 'Audio input',
  category: 'IO',
  //nodeType: 'input',
  inPortList: [],
  outPortList: [{ name: 'Audio out', desc: 'Audio output', type: 0 }],
  paramList: [],
};

export default function InputNode(props: NodeProps) {
  const portStyle = { width: '10px', height: '10px', background: '#555' };
  const { data } = props;

  const name = lib.objectDescriptors.get(data.dspID)!.name;

  return (
    <>
      <Handle
        type="source"
        id="0"
        position={Position.Bottom}
        style={portStyle}
        // onConnect={(params) => console.log('handle onConnect', params)}
      />
      <div className="nodeStyle">{name}</div>
    </>
  );
}
