import { Handle, Position, NodeProps } from 'react-flow-renderer';
import { ObjectDesc } from '../LibraryTypes';
import './node.global.css';

export const gateDesc: ObjectDesc = {
  dspID: 12,
  name: 'Gate',
  desc: 'Gate',
  category: 'Audio Effects',
  //nodeType: 'gate',
  inPortList: [{ name: 'Audio in', desc: 'Audio to be filtered', type: 0 }],
  outPortList: [{ name: 'Audio out', desc: 'Filtered audio', type: 0 }],
  paramList: [
    { name: 'Threshold dB', desc: 'Threshold', min: 0.0, max: 1.0, default: 0.5 },
    { name: 'Attack ms', desc: 'Attack ms', min: 0.1, max: 2000, default: 20 },
    { name: 'Release ms', desc: 'Release ms', min: 0.1, max: 2000, default: 200 },

  ],
};

export default function GateNode(props: NodeProps) {
  const { selected, data } = props;
  const selectedCss = selected ? 'selected' : '';
  const portStyle = { width: '10px', height: '10px', background: '#555' };

  return (
    <>
      {gateDesc.inPortList.map((_p, i) => {
        const ci = i.toString();
        return (
          <Handle
            id={ci}
            key={ci}
            type="target"
            position={Position.Top}
            style={portStyle}
            //  onConnect={(params) => console.log('handle onConnect', params)}
          />
        );
      })}
      <div className={`nodeStyle ${selectedCss}`}>
        <div className="ObjUiTitle" style={{ paddingBottom: '5px' }}>
          {data.name}
        </div>
        <hr className={`objSepStyle ${selectedCss}`} />

        {gateDesc.paramList.map((pl, i) => {
          return (
            <div key={pl.name.toString()}>
              <div
                className="ObjUiParamName"
                style={{ width: '70px', textAlign: 'right' }}
              >
                {pl.name}
              </div>
              <div
                className="ObjUiParamVal"
                style={{
                  paddingLeft: '10px',
                  width: '70px',
                  textAlign: 'left',
                }}
              >
                {props.data.params[i].value}
              </div>
            </div>
          );
        })}
      </div>
      {gateDesc.outPortList.map((p) => {
        return (
          <Handle
            key={p.name.toString()}
            type="source"
            position={Position.Bottom}
            style={portStyle}
            //  onConnect={(params) => console.log('handle onConnect', params)}
          />
        );
      })}
    </>
  );
}
