import React, {useState} from 'react';
import Switch from "react-switch";
import ProgressBar from 'react-bootstrap/ProgressBar';
import {Buffer} from 'buffer';

import './menu/menu.global.css';

import 'bootstrap/dist/css/bootstrap.min.css'

const handshake = new Uint8Array([0xf0, 0x9f, 0x91, 0x89, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0xf0, 0x9f, 0x91, 0x88]);
const getCurrentPatch = new Uint8Array([0xf0, 0x9f, 0x91, 0x89, 0x00, 0x02, 0x00, 0x00, 0x00,0x00, 0x00, 0x00, 0xf0, 0x9f, 0x91, 0x88]);
const packetHeader = new Uint8Array([0xf0, 0x9f, 0x91, 0x89]);
//const packetFooter = [0xf0, 0x9f, 0x91, 0x88];

const concatU8A = (buffer1:Uint8Array, buffer2:Uint8Array): Uint8Array => {
  let tmp = new Uint8Array(buffer1.byteLength + buffer2.byteLength);
  tmp.set(new Uint8Array(buffer1), 0);
  tmp.set(new Uint8Array(buffer2), buffer1.byteLength);
  return tmp;
};

function uintToString(uintArray: number[]) {
  const encodedString = String.fromCharCode.apply(null, uintArray);
  const decodedString = decodeURIComponent(escape(encodedString));
  return decodedString;
}

function bytesToU32(bytes: number[])
{
  let slots_msb = 0;
  for(let i=0;i<4;i++)
  {
    slots_msb |= bytes[i] << ((3-i)*8);
  }
  return slots_msb;
}

let keepPortOpen = true;

const readUntilClosed = async (port: SerialPort, readStream: ReadableStreamDefaultReader, connection: SerialConnection) => {
    while(port.readable && keepPortOpen) {
      try {
        while (true) {
          const { value, done } = await readStream.read();
          if (done) {
            break;
          }
          connection.parseIncoming(value);
        }
      } catch (error) {
        console.error(error);
      } finally {
        readStream.releaseLock();
      }
    }
  };

  interface CommsCommand {
    version: number;
    cmd: number;
    opt0: number;
    opt1: number;
    opt2: number;
    nData: number;
    data: number[];
  }

  enum ParseState {
    Header = 0,
    Cmd,
    Data,
    doCmd,
    Footer,
  }

  export enum CommsCommands {
    Hello = 0,
    BasicInfo,
    GetPatch,
    PatchData,
    SavePatch,
    DeletePatch,
    DspUpdate,
    Param,
    ParamValue,
    Control,
    CPU,
  }

export interface SerialConnectionProps {
  onPatchLoadCallback: (patchSlot: number, data: Buffer) => void;
}

export default class SerialConnection extends React.Component<SerialConnectionProps, { checked: boolean, deviceName: string}> {

  port: SerialPort | null = null;
  readStream: any;
  writeStream: any;
  status: string;
  
  rxCommand: CommsCommand;
  parseState: ParseState;
  dataBuff: Uint8Array;

  deviceModel: string;
  deviceSlot: number;
  ram: number;
  cpu: number;

  expectedSeqNum: number;
  concatBuff: Buffer;

  // TODO callbacks for setting:
  // - Slots array
  // - Patch
  // - Control Chnages
  // - Param changes

  //TODO methods for settings:
  // - Patch
  // - COntrols
  // - etc

  constructor(props: any) {
      super(props);
      this.state = { checked: false, deviceName: "" };
      this.handleChange = this.handleChange.bind(this);
      this.status = "Disconnected";
      //this.deviceName ="";
      this.deviceModel ="";
      this.deviceSlot = 0xff;
      this.ram = 50;
      this.cpu = 10;
      this.rxCommand = { version: 0, cmd: 0, opt0: 0, opt1: 0, opt2: 0, nData: 0, data: [] };
      this.parseState = ParseState.Header;
      this.dataBuff = new Uint8Array([]);
      this.expectedSeqNum = 0;
      this.concatBuff = Buffer.from([]);
    }

  checkCompat = () => {
      if ("serial" in navigator){
          console.log("Webserial is supported.")
      }
      else{
          console.log("Webserial is NOT supported.")
      }
  }

  handleChange(checked: Boolean) {

      if(checked) {
          this.getAccess();
      }
      else{
        keepPortOpen = false;
        this.readStream.cancel().then(() => {
          this.readStream.releaseLock();
          this.writeStream.releaseLock();
          this.port?.close();
          this.setState({ checked: false });
        });
        
      }
  }

  write = (data: number[]) => {
      const writer = this.writeStream.getWriter();
      writer.write(data);
      writer.releaseLock();
    
  }

  onConnect = () => { 
      this.setState({ checked: true }); 

      if(this.port)
      {
        keepPortOpen = true;
        this.readStream = this.port?.readable?.getReader();
        readUntilClosed(this.port, this.readStream, this);
        this.writeStream = this.port.writable?.getWriter();
        this.writeStream.write(handshake);
      }
  }

  onClose = () => { this.setState({ checked: false }); }

  getAccess = () => {
      navigator.serial.requestPort().then( port =>{
          this.port = port;
          this.port.onconnect = this.onConnect;
          this.port.ondisconnect = this.onClose;
          this.port.open({ baudRate: 115200 }).then(()=>{this.onConnect();});
      });
  }

  // Get bytes from serial port
  getBytes = (bytesToGet: number): Uint8Array | null => {
    if(this.dataBuff.length < bytesToGet){return null;}
    let bytes:Uint8Array = this.dataBuff.subarray(0, bytesToGet);
    //Remove the byte from the buffer
    this.dataBuff = this.dataBuff.subarray(bytesToGet);
    return bytes;
  };

      // Find header in incoming data
  seekHeader = (): boolean => {
    while(this.dataBuff.length > 3)
    {
      let firstFour = this.dataBuff.subarray(0, 4);
      if(packetHeader.every((byte, idx) => byte === firstFour[idx]))
      {
        this.dataBuff = this.dataBuff.subarray(4);
        return true;
      }
      this.dataBuff = this.dataBuff.subarray(1);
    }
    return false;
  };

  parseIncoming(inputArray: Uint8Array) {
    let dataAvailable = true;

    // Add data to buffer
    this.dataBuff = concatU8A(this.dataBuff, inputArray);

    while (dataAvailable) {
      switch (this.parseState) {
        case ParseState.Header: {
          // eslint-disable-next-line @typescript-eslint/no-unused-expressions
          this.seekHeader() ? (this.parseState = ParseState.Cmd) : (dataAvailable = false);
          break;
        }
        case ParseState.Cmd: {
          const cmdInfo = this.getBytes(7);
          // console.log('cmdInfo: ', cmdInfo);
          if (cmdInfo != null) {
            this.rxCommand.version = cmdInfo[0] as number;
            this.rxCommand.cmd = cmdInfo[1] as number;
            this.rxCommand.opt0 = cmdInfo[2] as number;
            this.rxCommand.opt1 = cmdInfo[3] as number;
            this.rxCommand.opt2 = cmdInfo[4] as number;
            this.rxCommand.nData = ((((cmdInfo[5] as number) & 0xff) << 8) | ((cmdInfo[6] as number) & 0xff)) as number;
          } else {
            dataAvailable = false;
          }
          this.rxCommand.data = [];
          this.parseState = ParseState.Data;
          break;
        }
        case ParseState.Data: {
          // Until we run out of data or we have read nBytes
          while (dataAvailable && this.rxCommand.data.length < this.rxCommand.nData) {
            const byte = this.getBytes(1);
            if (byte != null) {
              this.rxCommand.data.push(byte[0]);
            } else {
              dataAvailable = false;
            }
          }

          if (this.rxCommand.data.length === this.rxCommand.nData) {
            // console.log('Got command!', this.rxCommand);
            // TODO: Checksum and footer
            this.doCommand();
            //dataAvailable = false;
            // serialport.flush();
            this.parseState = ParseState.Header;
          }
          break;
        }

        default:
          break;
      }
    }// while data available
  }

  doCommand() {
    switch (this.rxCommand.cmd) {
      case CommsCommands.BasicInfo: {
        // Update UI with 'connected', name and model
        const currentSlot = this.rxCommand.opt0;
        const nameArray = this.rxCommand.data.splice(0, 64);
        const modelArray = this.rxCommand.data.splice(0, 64);
        let strLen = nameArray.findIndex((n) => n === 0);
        this.setState({deviceName: uintToString(nameArray.slice(0, strLen))}); 
        strLen = modelArray.findIndex((n) => n === 0);
        this.deviceModel = uintToString(modelArray.slice(0, strLen));
        const MSBnums = this.rxCommand.data.splice(0, 4);
        const LSBnums = this.rxCommand.data.splice(0, 4);
        let slotsMSB = bytesToU32(MSBnums);
        let slotsLSB = bytesToU32(LSBnums);
        let getPatch = getCurrentPatch;
        getPatch[6] = currentSlot;
        this.writeStream.write(getPatch);
        break;
      }
      case CommsCommands.PatchData: {
        // TODO if there are problems here, clear concat buff..
        if(this.expectedSeqNum != this.rxCommand.opt1){break;} //Error?
        if(this.rxCommand.opt2 == 0xFF){ console.log("No patch file found!"); break;} //Error?
        let buff = Buffer.from(this.rxCommand.data);
        this.concatBuff = Buffer.concat([this.concatBuff, buff]);
        this.expectedSeqNum += 1;
        console.log("Got patch part..");
        console.log(this.concatBuff);
        console.log("Buff size: ", this.concatBuff.length);
        if(this.rxCommand.opt2 == 1)
        {
          console.log("Got patch!");
          console.log(this.concatBuff);
          console.log("Buff size: ", this.concatBuff.length);
          this.props.onPatchLoadCallback( this.rxCommand.opt0, this.concatBuff);
          this.concatBuff = Buffer.from([]);
          this.expectedSeqNum = 0;
        }
        break;
      }
    }
  }
    
    render() {
      let titleBarContent = <div>Disconnected</div>;
      if(this.state.checked)
      {
        titleBarContent = 
        <div style={{display:'inline-block', width:'1000px'}}>
          <div className="titleBar-item" style={{width:'400px', marginTop:'5px'}}>{this.state.deviceName}  ({this.deviceModel})</div>
          <ProgressBar className="titleBar-item titleBar-progress"  now={this.ram} label={`RAM: ${this.ram}%`} variant="success"/>
          <ProgressBar className="titleBar-item titleBar-progress"  now={this.cpu} label={`CPU: ${this.cpu}%`} variant="progress-bar"/>
        </div>
      }
      return (
        <>
          <Switch className="titleBar-item" onChange={this.handleChange} checked={this.state.checked} />
          {titleBarContent}
        </>
      )
    }
  }
