import { Handle, Position, NodeProps } from 'react-flow-renderer';
import { ObjectDesc, ObjectData } from '../LibraryTypes';
import { lib } from '../Library';

export default function GenericNode(props: NodeProps) {
    const { selected, data } = props;
    const descriptor: ObjectDesc = lib.objectDescriptors.get(data.dspID)!;
    const selectedCss = selected ? 'selected' : '';
    const portStyle = { width: '10px', height: '10px', background: '#555' };

    const displayParams = () => {

      let retCode:JSX.Element[] = [];

      retCode.push(<div key={"k0"} style={{height:'10px'}} />);
      descriptor.paramList.map((p, i) => {
        const key = i.toString();
        retCode.push(     
            <div key={key}>
              <div key={key+'a'} className="ObjUiParamName" style={{ width: '70px', textAlign: 'right' }}>
                {p.name} :
              </div>
              <div key={key+'b'} className="ObjUiParamVal" style={{ paddingLeft: '5px', width: '70px', textAlign: 'left'}}>
                {data.params[i].value}
              </div>
            </div>
        );
      });
      retCode.push(<div key={"k1"} style={{height:'10px'}} />);
      return retCode;
    };

    return (
      <>
        {descriptor.inPortList.map((_p, i) => {
          const ci = i.toString();
          return (
            <Handle
              id={ci}
              key={ci}
              type="target"
              position={Position.Top}
              style={portStyle}
            />
          );
        })}
        <div className={`nodeStyle ${selectedCss}`}>
          <>
          <div className="ObjUiTitle" style={{ paddingBottom: '5px' }}>
            {descriptor.name}
          </div>
          {descriptor.paramList.length > 0? displayParams() : <></>}
          </>
        </div>
        {descriptor.outPortList.map((p) => {
          return (
            <Handle
              key={p.name.toString()}
              type="source"
              position={Position.Bottom}
              style={portStyle}
            />
          );
        })}
      </>
    );
  }
  