import { lib } from '../library/Library';
import {ObjectDesc} from '../library/LibraryTypes';
import {Popover, OverlayTrigger} from 'react-bootstrap';

import './menu.global.css';


export default function LibraryLayout() {
  const onDragStart = (event: any, nodeType: any) => {
    event.dataTransfer.setData('application/reactflow', nodeType);
    event.dataTransfer.effectAllowed = 'move';
  };

  const popover = (desc:ObjectDesc )=>(
    <Popover id="popover-basic">
      <Popover.Header as="h3">{desc.name}</Popover.Header>
      <Popover.Body>{desc.desc}</Popover.Body>
    </Popover>
  );

  return (
    <div className="libraryLayout">
      <div className="menuTitle">Library</div>
      {lib.categories.map((category, i) => {
        return (
          <div key={i.toString()}>
            <>
              <div key={i.toString()} className="librarySubTitle">
                {category.name}
              </div>
              {
                Array.from(lib.objectDescriptors.values()).map((descriptor, key) => 
                  descriptor.category === category.name ? (
                    <div key={key.toString()}>
                      <div key={key.toString()} className="libraryObjectContainer">
                        {/* hover-hover thing: https://github.com/react-bootstrap/react-bootstrap/issues/5027 */}
                      <OverlayTrigger trigger={["hover", "hover"]} placement="right" overlay={popover(descriptor)} delay={{ show: 1000, hide: 0 }}>
                        <div
                          key={key.toString()}
                          className="dndnode"
                          onDragStart={(event) =>
                            onDragStart(event, descriptor.dspID)
                          }
                          draggable
                        >
                          {descriptor.name}
                        </div>
                        </OverlayTrigger>
                      </div>                  
                    </div>
                  ) : null
              )}
            </>
          </div>
        );
      })}
    </div>
  );
}
