import { Handle, Position, NodeProps } from 'react-flow-renderer';
import { ObjectDesc } from '../LibraryTypes';
import './node.global.css';

export const sawwaveDesc: ObjectDesc = {
  dspID: 13,
  name: 'Saw Wave',
  desc: 'Saw wave generator. \n \r Anti-alisaed sawtooth wave.',
  category: 'Generators',
  //nodeType: 'sawwave',
  inPortList: [],
  outPortList: [{ name: 'Audio out', desc: 'Audio output', type: 0 }],
  paramList: [
    { name: 'Frequency', desc: 'Frequency', min: 0, max: 20000, default: 220 },
  ],
};

export default function SawwaveNode(props: NodeProps) {
  const { selected, data } = props;
  const selectedCss = selected ? 'selected' : '';
  const portStyle = { width: '10px', height: '10px', background: '#555' };

  return (
    <>
      <div className={`nodeStyle ${selectedCss}`}>
        <div className="ObjUiTitle" style={{ paddingBottom: '5px' }}>
          {data.name}
        </div>
        <hr className={`objSepStyle ${selectedCss}`} />

        {sawwaveDesc.paramList.map((pl, i) => {
          return (
            <div key={pl.name}>
              <div
                className="ObjUiParamName"
                style={{ width: '70px', textAlign: 'right' }}
              >
                {pl.name}
              </div>
              <div
                className="ObjUiParamVal"
                style={{
                  paddingLeft: '10px',
                  width: '70px',
                  textAlign: 'left',
                }}
              >
                {data.params[i].value}
              </div>
            </div>
          );
        })}
      </div>
      {sawwaveDesc.outPortList.map((_p, i) => {
        const ci = i.toString();
        return (
          <Handle
            key={ci}
            id={ci}
            type="source"
            position={Position.Bottom}
            style={portStyle}
            //  onConnect={(params) => console.log('handle onConnect', params)}
          />
        );
      })}
    </>
  );
}
