import NumericInput from 'react-numeric-input';
import Select from 'react-select';
import { ObjectParam, ObjectData } from '../library/LibraryTypes';
import { lib } from '../library/Library';
import { Node } from 'react-flow-renderer';
import './menu.global.css';

const customStyles = {
  menu: (provided: any) => ({
    ...provided,
    backgroundColor: 'var(--bgGrey)',
    color: 'whitesmoke',
  }),
  control: (provided: any, state: any) => ({
    ...provided,
    backgroundColor: state.isSelected ? 'var(--bgGrey2)' : 'var(--bgGrey)',
    color: 'whitesmoke',
    width: '150px',
  }),
  option: (provided: any, state: any) => ({
    ...provided,
    backgroundColor: state.isSelected ? 'var(--bgGrey2)' : 'var(--bgGrey)',
    color: 'whitesmoke',
    ':hover': {
      backgroundColor: 'var(--bgGrey2)',
    },
  }),
  singleValue: (provided: any, state: any) => ({
    ...provided,
    backgroundColor: state.isSelected ? 'var(--bgGrey2)' : 'var(--bgGrey)',
    color: 'whitesmoke',
  }),
};

export interface PropertiesLayoutProps {
  controls: number[];
  selObj: Node<ObjectData>;
  onParamChange: (
    params: ObjectParam[],
    index: number,
    valueOnly: boolean
  ) => void;
}

export default function PropertiesLayout(props: PropertiesLayoutProps) {
  const { controls, selObj, onParamChange } = props;
  const opts = [{ value: 999, label: 'Fixed' }];

  const shapeOpts = [
    { value: 0, label: 'Linear' },
    { value: 1, label: 'Exp' },
    { value: 2, label: 'Para' },
  ];
  
  const numboxStyle = {
    input: {
      height: '35px',
      padding: '10px',
      color: 'var(--accentCol)',
      fontSize: '16px',
      background: 'var(--bgGrey)',
    },
  };

  let availableParams = 0;
  let paramsCpy: any = null;

  if(selObj === null)
  {
    availableParams = -1;
  }
  else
  {
    availableParams = selObj.data.params.length;
    paramsCpy = [...props.selObj?.data?.params];
  }

  controls.forEach((_c: number, i: number) => {
    opts.push({ value: i, label: "Control "+i.toString()} );
  });


  switch(availableParams)
  {
    case -1:
      return (
          <>
            <div className="inspectorLayout">
              <div className="menuTitle">Properties</div>      
              Select a single object.
            </div> 
          </>
      );
      break;

      case 0:
        return (
            <>
              <div className="inspectorLayout">
                <div className="menuTitle">Properties</div>      
                This object has no properties
              </div> 
            </>
        );
        break;

        default:
          return (
            <div className="inspectorLayout">
              <>
                <div className="menuTitle">Properties</div>       


                {
                  lib.objectDescriptors.get(selObj.data.dspID)?.paramList.map((c, i) => {
                    const key = i.toString();
                    return(
                    <div key={key}>
                      <div className="inspectorSubTitle">{c.name}</div>

                      <div className="inspectorProp">
                        <div className="inspectorLabel">Control:</div>
                        <Select
                          styles={customStyles}
                          options={opts}
                          value={
                            opts[
                              props.selObj.data.params[i].fixed
                                ? 0
                                : props.selObj.data.params[i].ctlIdx + 1
                            ]
                          }
                          onChange={(opt: any) => {
                            if (opt!.label === 'Fixed') {
                              paramsCpy[i].fixed = true;
                            } else {
                              paramsCpy[i].fixed = false;
                              paramsCpy[i].ctlIdx = opt?.value;
                            }
                            props.onParamChange(paramsCpy, i, false);
                          }}
                        />
                      </div> {/*  Inspector prop */}
      
                      {props.selObj.data.params[i].fixed ? (
                        <div className="inspectorProp">
                          <div className="inspectorLabel">Value:</div>
                          <NumericInput
                            min={c.min}
                            max={c.max}
                            value={props.selObj.data.params[i].value}
                            step={0.1}
                            precision={2}
                            size={5}
                            style={numboxStyle}
                            onChange={(val: any) => {
                              if (val != null) {
                                paramsCpy[i].value = val;
                                onParamChange(paramsCpy, i, true);
                              }
                            }}
                          />
                        </div>
                      ) : (
                        <>
                          <div className="inspectorProp">
                            <div className="inspectorLabel">Min:</div>
                            <NumericInput
                              min={c.min}
                              max={c.max}
                              value={props.selObj.data.params[i].mapMin}
                              step={0.1}
                              precision={2}
                              size={5}
                              style={numboxStyle}
                              onChange={(val: any) => {
                                if (val != null) {
                                  paramsCpy[i].mapMin = val;
                                  onParamChange(paramsCpy, i, false);
                                }
                              }}
                            />
                          </div>
                          <div className="inspectorProp">
                            <div className="inspectorLabel">Max:</div>
                            <NumericInput
                              min={c.min}
                              max={c.max}
                              value={props.selObj.data.params[i].mapMax}
                              step={0.1}
                              precision={2}
                              size={5}
                              style={numboxStyle}
                              onChange={(val: any) => {
                                if (val != null) {
                                  paramsCpy[i].mapMax = val;
                                  onParamChange(paramsCpy, i, false);
                                }
                              }}
                            />
                          </div>
                          <div className="inspectorProp">
                            <div className="inspectorLabel">Shape:</div>
                            <Select
                              styles={customStyles}
                              options={shapeOpts}
                              value={shapeOpts[props.selObj.data.params[i].mapShape]}
                              onChange={(opt: any) => {
                                paramsCpy[i].mapShape = opt?.value;
                                onParamChange(paramsCpy, i, false);
                              }}
                            />
                          </div>
                        </>
                      )}

                    </div>);
                })
              }


              </>
            </div>
          );
          break;
  }

  

}
