/* eslint-disable no-nested-ternary */
/* eslint-disable jsx-a11y/alt-text */
import * as FaIcons from 'react-icons/fa';
import {ButtonGroup, Button} from 'react-bootstrap';
import './menu.global.css';
import LibraryLayout from './LibraryLayout';
import PropertiesLayout from './PropertiesLayout';
import SaveLoadLayout from './SaveLoadLayout';
import { ObjectParam } from '../library/LibraryTypes';
import { Node } from 'react-flow-renderer';

export interface MenuProps {
  show: boolean;
  activeTab: number;
  controls: number[];
  selObj: Node;
  slots: number[];
  currentSlot: number;
  connected: boolean;
  onSetVisible: (show: boolean) => void;
  onSetActiveTab: (tab: number) => void;
  onParamChange: (
    params: ObjectParam[],
    index: number,
    valueOnly: boolean
  ) => void;
  onDeviceLoad: (slot: number) => void;
  onDeviceSave: (slot: number) => void;
  onDeviceDelete: (slot: number) => void;
  onLocalSave: () => void;
  onLocalLoad: (patchSlot: number, patchBuffer: Buffer) => void;
}

export function Menu(props: MenuProps) {
  const {
    show,
    activeTab,
    controls,
    selObj,
    slots,
    currentSlot,
    connected,
    onSetVisible,
    onSetActiveTab,
    onParamChange,
    onDeviceLoad,
    onDeviceSave,
    onDeviceDelete,
    onLocalSave,
    onLocalLoad,
  } = props;

  return (
    <>
      <div className={show ? 'menu_show menu' : 'menu_hide menu'}>

      <ButtonGroup style={{ gridColumn: '1/ span 4'}}>
        <Button className="menuTabs" onClick={() => onSetActiveTab(0)} variant="secondary" active={activeTab==0?true:false}>Library</Button>
        <Button className="menuTabs" onClick={() => onSetActiveTab(1)} variant="secondary" active={activeTab==1?true:false}>Properties</Button>
        <Button className="menuTabs" onClick={() => onSetActiveTab(2)} variant="secondary" active={activeTab==2?true:false}>Load/Save</Button>
        <Button className="menuTabs" onClick={() => onSetActiveTab(3)} variant="secondary" active={activeTab==3?true:false}>Advanced</Button>
      </ButtonGroup>
        {
          {
            0: <LibraryLayout />,
            1: <PropertiesLayout controls={controls} selObj={selObj} onParamChange={onParamChange} />,
            2: <SaveLoadLayout  connected={connected} slots={slots} currentSlot={currentSlot} onSlotLoad={onDeviceLoad} onSlotSave={onDeviceSave} onSlotDelete={onDeviceDelete} onLocalLoad={onLocalLoad} onLocalSave={onLocalSave}/>,
            3: <div/>
          }[activeTab]
        }
      </div>
    </>
  );
}
